body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
    text-align: center;
    background: white;
    max-height: 100%;
}

.title {
    margin: 0;
    line-height: 1em;
    font-family: Khand, sans-serif;
    font-size: 500%;
    letter-spacing: 0.04em;
    background: linear-gradient(to right, #2F5A91 31%, #767070 31%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.navBox {
    flex-direction: row;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
    .header img {
        display: none;
        
    }
    .navBox {
        flex-direction: column-reverse;
    }
    .title {
        font-size: 350%;
    }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
    .header img {
        display: none;
    }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
    .header img {
        display: none;
    }
}
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
    .header img {
        display: inline;
    }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
    .header img {
        display: inline;
    }
}

.containerBody {
    width: 100%;
    justify-content: center;
}

.rowBody {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 70%;
}

.rowBody h1 {
    font-family: Khand;
    color: #2F5A91;
    margin-top: 100%;
    font-weight: 300;
    text-align: center;
    font-weight: bold;
    background: white;
    padding: 5%;
}

.rowBody h1:hover {
    color: #767070;
}
  
.columnBody {
    display: flex;
    flex-direction: column;
    flex-basis: 100%;
    flex: 1 1;
    justify-content: center;
    opacity: 1.0;
    filter: alpha(opacity=100);
}

.columnBody:hover {
    transform: scale(1.02);
    opacity: 0.7;
    filter: alpha(opacity=40);
}

.technicalBody, .planningBody, .organisationalBody {
    background-repeat: no-repeat;
    background-size: cover;
    height: 75vh;
    width: 100%;
    margin: 0.5em;
}

.footer {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    border-top: solid;
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    font-family: Khand;
    font-size: 200%;
    color: #767070;
    text-align: center;
}

.footer p {
    color: #2F5A91;
    opacity: 1.0;
    filter: alpha(opacity=100);
} 

.footer p:hover {
    opacity: 70%;
    opacity: 0.7;
    filter: alpha(opacity=40);
}

@media screen and (max-width: 800px) {
    .columnBody {
      flex: 1 1
    }
    .rowBody {
        width: 100%;
        flex-direction: column;
    }
    .technicalBody, .planningBody, .organisationalBody {
        height: 25vh;
        padding: 0;
    }
    .containerBody h1 {
        margin-top: 20%;
        padding: 0;
    }

    .footer {
        width: 100%;
    }
}
